import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Storage } from '@ionic/storage';
import * as $ from 'jquery';
import 'rxjs/add/operator/map';


@Injectable()
export class GetChildrenProvider {
	children: any = [];
	tags: any = [];

	constructor(public http: Http, private storage: Storage) {
		console.log('Hello GetChildrenProvider Provider');
	}


	getAllChildren(token){
		return new Promise((resolve) => {

			var settings = {
			  "async": true,
			  "crossDomain": true,
			  // "url": "http://ec2-18-220-223-50.us-east-2.compute.amazonaws.com:9876/children?token=eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..JuJL7wU1yvTjMTey8ZchMKPjOfAGGk6__kXiqMu7Qko",
			  "url": "http://ec2-18-220-223-50.us-east-2.compute.amazonaws.com:9876/children?token="+token,
			  "method": "POST",
			  "headers": {
			    "content-type": "application/json",
			    "cache-control": "no-cache",
			    "postman-token": "4935ed16-98ed-c035-1572-aeb484e2838a",
			    "Access-Control-Allow-Methods": "GET,PUT,POST,DELETE,PATCH,OPTIONS",
		        "Access-Control-Allow-Headers": "Origin, X-Requested-With, Content-Type, Accept, Key",
		        "Access-Control-Allow-Origin":"*",
		        "Allow-Control-Allow-Origin":"*"
			  }
			}
			
			$.ajax(settings).done((response)=> {
			  	if(response.success)
		      	{
		    		$.each(response.data, (index, value)=>{
				   		value["tag"] = index;
				   		this.tags.push(index);
				   		this.children.push(value);
						this.storage.set("tags", this.tags);
						this.storage.set("children", this.children);

			        });
			        resolve(true);
		      	}
		      	else{
		       		alert("Not allowed to access children");
		       		resolve(false);
		      	}
			}).fail((err)=>{
				alert("error in getting children")
				resolve(false);
			});

		});
	}

}



